<template>
    <div class="m-dashboard m-dashboard-work">
        <!-- <el-input
            class="m-dashboard-work-search"
            placeholder="请输入内容"
            v-model="search"
            @change="searchPost"
        >
            <el-select
                class="u-select"
                v-model="searchType"
                slot="prepend"
                placeholder="请选择"
                @change="searchPost"
            >
                <el-option
                    :label="item"
                    :value="key"
                    v-for="(item, key) in types"
                    :key="key"
                ></el-option>
            </el-select>
            <el-button
                slot="append"
                icon="el-icon-search"
                @click="searchPost"
            ></el-button>
        </el-input> -->

        <div class="m-dashboard-box">
            <ul class="m-dashboard-box-list" v-if="data.length">
                <li v-for="(item, i) in data" :key="i">
                    <i class="u-icon">
                        <img
                            v-if="item.status > 0"
                            svg-inline
                            src="../assets/img/works/repo.svg"
                        />
                        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 12 16" width="12" height="16" fill="#dbab09" v-else="" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path fill-rule="evenodd" d="M4 13H3v-1h1v1zm8-6v7c0 .55-.45 1-1 1H1c-.55 0-1-.45-1-1V7c0-.55.45-1 1-1h1V4c0-2.2 1.8-4 4-4s4 1.8 4 4v2h1c.55 0 1 .45 1 1zM3.8 6h4.41V4c0-1.22-.98-2.2-2.2-2.2-1.22 0-2.2.98-2.2 2.2v2H3.8zM11 7H2v7h9V7zM4 8H3v1h1V8zm0 2H3v1h1v-1z"/></svg>
                    </i>
                    <a class="u-title" target="_blank" :href="postLink(item.id)"
                        >[{{ item.type | typeFormat }}]
                        {{ item.title || "无标题" }}</a
                    >
                    <div class="u-desc">
                        <span class="u-desc-subitem"
                            >编号 : <b>{{ item.id }}</b></span
                        >
                        <span class="u-status u-desc-subitem">
                            状态:
                            <b
                                :class="{
                                    pass: item.status > 0,
                                    pending: item.status == 0,
                                    fail: item.status < 0,
                                }"
                                >{{ statusmap[item.status] }}</b
                            >
                        </span>
                        <time class="u-time u-desc-subitem"
                            >提交于: {{ item.createTime | dateFormat }}</time
                        >
                    </div>
                    <el-button-group class="u-action">
                        <el-button
                            v-if="item.status < 1"
                            type="primary"
                            size="mini"
                            icon="el-icon-edit"
                            title="编辑"
                            @click="edit(searchType, item.id)"
                        ></el-button>
                    </el-button-group>
                </li>
            </ul>
            <el-alert
                v-else
                class="m-dashboard-box-null"
                title="没有找到相关条目"
                type="info"
                center
                show-icon
            >
            </el-alert>
            <el-pagination
                class="m-dashboard-box-pages"
                background
                :page-size="per"
                :hide-on-single-page="true"
                @current-change="changePage"
                :current-page.sync="page"
                layout="total, prev, pager, next, jumper"
                :total="total"
            >
            </el-pagination>
        </div>
    </div>
