<script>
import { getQuestions } from "../service/exam";
import dateFormat from "../utils/dateFormat";
import { types } from "../assets/data/exam.json";
const statusmap = {
    "-2": "已删除",
    "-1": "未通过审核",
    "0": "待审核",
    "1": "已入库",
};
export default {
    name: "ideas",
    props: [],
    data: function() {
        return {
            data: [],
            total: 1,
            page: 1,
            per: 15,
            search: "",
            searchType: "question",
            types: {
                question: "题目",
                paper: "试卷",
            },
            statusmap,
        };
    },
    computed: {},
    methods: {
        loadPosts: function(i = 1) {
            getQuestions(
                {
                    pageIndex: i,
                    // title: this.search,
                    // type: this.searchType,
                    // per : this.per
                },
                this
            ).then((res) => {
                this.data = res.data.data;
                this.total = res.data.page.total;
            });
        },
        changePage: function(i = 1) {
            this.loadPosts(i);
        },
        searchPost: function() {
            this.loadPosts();
        },
        edit: function(type, id) {
            location.href = "./publish/#/exam/" + type + "/" + id;
        },
        del: function(id) {
            this.$alert("确定要删除吗？", "确认信息", {
                confirmButtonText: "确定",
                callback: (action) => {
                    if(action == 'confirm'){
                        delPost(id)
                            .then(() => {
                                this.$message({
                                    type: "success",
                                    message: `删除成功`,
                                });
                                location.reload();
                            })
                    }
                },
            });
        },
        draft: function(id, i) {
            hidePost(id)
                .then((res) => {
                    this.$message({
                        type: "success",
                        message: `操作成功`,
                    });
                    this.data[i].post_status = "draft";
                })
        },
        publish: function(id, i) {
            publishPost(id)
                .then((res) => {
                    this.$message({
                        type: "success",
                        message: `操作成功`,
                    });
                    this.data[i].post_status = "publish";
                })
        },
        postLink: function(id) {
            return "/exam/#/question/" + id;
        },
        paperLink: function(id) {
            return "/exam/#/paper/" + id;
        },
    },
    filters: {
        dateFormat: function(val) {
            return dateFormat(new Date(val * 1000));
        },
        typeFormat: function(type) {
            return types[type];
        },
    },
    created: function() {
        this.loadPosts();
    },
};
</script>

